body {
  margin: 0;
  background-color: #056679;
  font-family: sans-serif;
}
.todo-container {
  width: 350px;
  text-align: center;
  margin: auto;
  margin-top: 40px;
}
h1 {
  margin: 0;
  margin-bottom: 15px;
  color: #08869e;
  text-shadow: 0 4px 6px #00000066;
}
.input-section {
  background-color: #055565;
  padding: 18px;
  border-radius: 10px;
  box-shadow: 0 4px 6px #00000066;
}

.input-section input {
  border-radius: 5px;
  border: 1px, solid #106879;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  background-color: #055565;
  outline: none;
  color: #08869e;
}
input::placeholder {
  color: #08869e;
}

ul {
  text-align: left;
  padding: 0;
  overflow-x:hidden;
  white-space:nowrap; 
}
ul li {
  display: flex;
  list-style: none;
  background-color: #055565;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 10px;
  color: #08869e;
  box-shadow: 0 4px 6px #00000066;
}

ul li i {
  text-align: right;
  cursor: pointer;
}
ul li #edit {
  flex: 1;
  margin-right: 10px;
}
